@import "../../../styles/variable";
.title{
  color: $colorWhite;
  font-size: 34px;
  margin: 20px 0;
}
.text{
  font-size: 24px;
  color: $colorWhite;
}




.SliderCon{
  background-color: rgba(242, 224, 237, 0.611);
  padding: 20px;
  border-radius: 25px;
}
.SliderTitle{
  display: flex;
  align-items: center;
  justify-content: center;
}
.SliderTitle h3{
  font-size: 45px;
  font-weight: 600;
  text-shadow: 5px 5px 6px black;
  animation: Neon 5s linear 0s  infinite alternate;

}
.SliderWrap h3{
  font-size: 24px;
  width: 100%;
  color: rgb(43, 0, 255);
  font-weight: 500;
  animation:Zagtwo 5s linear 0s infinite alternate;
}
.SliderText{
  font-size: 20px;

}
.SliderWrap{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.SliderImg{
  height: 60px;
  display: flex;
  align-self: center;
  margin-left: 30px;
}
.SliderImg img{
  width: 100%;
  height: auto;
  object-fit: contain;
}
.ImgSvg{
  margin-left: 25px;
}



@keyframes Neon{
  0%{
    text-shadow: 5px 5px 15px rgb(255, 6, 6);
    color: rgb(0, 255, 247);
  }
  50%{
    text-shadow: 5px 5px 10px rgb(255, 128, 0);
    color: rgb(2, 157, 28);
  }

  100%{
    text-shadow:5px 5px 20px rgb(50, 251, 67);
    color: rgb(230, 6, 255);
  }
}
@keyframes clipMe {
  0% {
    transform: scale(0.2);
  }
  100%{
    transform: scale(1);
  }
}

@keyframes Zagtwo {
  0%, 100%{
    text-shadow: 5px 5px 30px rgb(74, 255, 33);
    transform: scale(1.5);
  }
  50%{
    transform: scale(0.8);
    text-shadow: 5px 5px 15px rgb(67, 0, 255);
  }
}
