.container{
  display: flex;
  justify-content: center;
  height: auto;
  max-width: 968px;
  width: 100%;
  margin: 0 auto;
  border-radius: 25px;
  position: relative;
  box-shadow: 0px 0px 10px 2px rgba(5, 3, 3, 0.714) inset;
  background: linear-gradient(135deg, rgb(255, 254, 219), rgb(0, 196, 225));
  min-height: 100%;
}
.wrapLayout{
  padding: 40px 20px;
  overflow: hidden;
}
.center{
  align-items: center;
}

